import React from 'react'
// import {data} from './Data'
import star from '../../images/svg-star.svg'
import walkdog from '../../images/svg-walkdog.svg'
import snowglass from '../../images/svg-snowglass.svg'
import {
    ProjectsContainer,
    Projectstitlewrapper,
    ProjectsH1,
    Projectswrapper,
    ProjectsContentwrapper,
    Projectsinfo,
    Img,
    ProjectsTitle,
    ProjectsText,
} from './ProjectsElements'



const Projects = () => {

    return (
       <ProjectsContainer id="Services">
           <Projectswrapper>
                <Projectstitlewrapper>
                    <ProjectsH1>My Services</ProjectsH1>
                </Projectstitlewrapper>
                <ProjectsContentwrapper>
                    <Projectsinfo >
                        <Img src={star} />
                        <ProjectsTitle key="1">LoremLorem</ProjectsTitle>
                        <ProjectsText>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum is simply dummy text of the printing and typesetting industry. </ProjectsText>
                    </Projectsinfo>
                    <Projectsinfo>
                        <Img src={walkdog} />
                        <ProjectsTitle>simply dummy</ProjectsTitle>
                        <ProjectsText>Lorem Ipsum is simply dummy text of the printing and typesetting industry. </ProjectsText>
                    </Projectsinfo>
                    <Projectsinfo >
                        <Img src={snowglass} />
                        <ProjectsTitle>Lorem Ipsum</ProjectsTitle>
                        <ProjectsText>Lorem Ipsum is simply dummy text of the printing and typesetting industry.  </ProjectsText>
                    </Projectsinfo>
                </ProjectsContentwrapper>
            </Projectswrapper>
       </ProjectsContainer>
    )
}

export default Projects
